/**
 * TopNavigation Component Tests - Streamlined
 * Testing core navigation functionality only (Reduced from 109 â†’ 10 tests)
 */

import { render, screen, fireEvent, waitFor } from '@testing-library/react';
import userEvent from '@testing-library/user-event';
import React from 'react';

import { TopNavigation } from '../TopNavigation';

// Mock next/navigation
const mockPush = jest.fn();
jest.mock('next/navigation', () => ({
  useRouter: () => ({
    push: mockPush,
    replace: jest.fn(),
    back: jest.fn(),
    forward: jest.fn(),
    refresh: jest.fn(),
    prefetch: jest.fn(),
  }),
}));

describe('TopNavigation Component', () => {
  beforeEach(() => {
    jest.clearAllMocks();
  });

  // Core rendering test
  it('should render navigation with essential elements', () => {
    render(<TopNavigation />);
    
    expect(screen.getByText('DATEN-SEE')).toBeInTheDocument();
    expect(screen.getByText('Zur Dashboard-Ãœbersicht')).toBeInTheDocument();
    expect(screen.getByText('ðŸ› Debug Dashboard')).toBeInTheDocument();
    expect(screen.getByLabelText('MenÃ¼ Ã¶ffnen')).toBeInTheDocument();
  });

  // Navigation functionality test
  it('should handle navigation clicks', async () => {
    const user = userEvent.setup();
    render(<TopNavigation />);

    const dashboardLink = screen.getByText('Zur Dashboard-Ãœbersicht');
    await user.click(dashboardLink);
    
    expect(mockPush).toHaveBeenCalledWith('/dashboard');
  });

  // Mobile menu toggle test
  it('should toggle mobile menu', async () => {
    const user = userEvent.setup();
    render(<TopNavigation />);

    const menuButton = screen.getByLabelText('MenÃ¼ Ã¶ffnen');
    await user.click(menuButton);
    
    // Mobile menu should be visible
    expect(screen.getByRole('navigation')).toBeInTheDocument();
  });

  // Settings menu test
  it('should open settings menu', async () => {
    const user = userEvent.setup();
    render(<TopNavigation />);

    const settingsButton = screen.getByLabelText('Einstellungen');
    await user.click(settingsButton);
    
    // Settings dropdown should appear
    await waitFor(() => {
      expect(screen.getByText('Profil')).toBeInTheDocument();
    });
  });

  // Logo click navigation test
  it('should navigate to home on logo click', async () => {
    const user = userEvent.setup();
    render(<TopNavigation />);

    const logo = screen.getByText('DATEN-SEE');
    await user.click(logo);
    
    expect(mockPush).toHaveBeenCalledWith('/');
  });

  // Keyboard navigation test
  it('should handle keyboard navigation', async () => {
    render(<TopNavigation />);

    const dashboardLink = screen.getByText('Zur Dashboard-Ãœbersicht');
    
    fireEvent.keyDown(dashboardLink, { key: 'Enter' });
    expect(mockPush).toHaveBeenCalledWith('/dashboard');
  });

  // External link test
  it('should handle external links correctly', () => {
    render(<TopNavigation />);

    const communityLink = screen.getByText('Community');
    expect(communityLink.closest('a')).toHaveAttribute('target', '_blank');
    expect(communityLink.closest('a')).toHaveAttribute('rel', 'noopener noreferrer');
  });

  // Responsive behavior test
  it('should hide desktop menu on small screens', () => {
    // Mock window size
    Object.defineProperty(window, 'innerWidth', {
      writable: true,
      configurable: true,
      value: 640, // Small screen
    });

    render(<TopNavigation />);
    
    const desktopNav = screen.getByRole('navigation');
    expect(desktopNav).toHaveClass('hidden', 'md:flex');
  });

  // Search functionality test
  it('should handle search input', async () => {
    const user = userEvent.setup();
    render(<TopNavigation />);

    const searchInput = screen.getByPlaceholderText('Dashboards durchsuchen...');
    await user.type(searchInput, 'test search');
    
    expect(searchInput).toHaveValue('test search');
  });

  // User menu test
  it('should display user information in menu', async () => {
    const user = userEvent.setup();
    render(<TopNavigation />);

    const userButton = screen.getByLabelText('Benutzerkonto');
    await user.click(userButton);
    
    await waitFor(() => {
      expect(screen.getByText('Abmelden')).toBeInTheDocument();
    });
  });
});